.imgfilm {
  width: 240px;
  margin-left: 5px;
  border-style: solid;
  border-color: #86719f;
  border-radius: 10px;
  &:hover {
    border-color: #452c63;
    transform: scale(1.02);
    z-index: 1;
  }
}
#blockimg:hover + .titled {
  background-color: bisque;
}
.imgfilmsmall {
  width: 220px;
  margin-left: 5px;
  border-style: solid;
  border-color: #86719f;
  border-radius: 10px;
  &:hover {
    border-color: #452c63;
    transform: scale(1.01);
    z-index: 1;
  }
}
.imgfilmlarge {

  width: 30%;
  margin-top: 5px;
  margin-left: 2%;
  border-style: solid;
  border-radius: 10px;
  border-color: #86719f;
  &:hover {
    border-color: #452c63;
    transform: scale(1.02);
    z-index: 1;
  }
}

.rowmovies {
  margin: 10px;
  display: flex;
  max-width: fit-content;
  margin-left: 2px;
  font-size: 15px;
  background-color: #452c63;
}
.disflex {
  margin: 10px;
  display: flex;
  margin-left: 2px;
  font-size: 15px;
  background-color: #cec3de;
}
.disgrid {
  display: flex;
  flex-wrap: wrap;
}
.bar::-webkit-scrollbar {
  height: 10px;
}
.rowbox {
  overflow-x: scroll;
  overflow-y: hidden;

  &::-webkit-scrollbar {
    height: 8px;
  }
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px #5209bf;
    border-radius: 10px;
  }
  &::-webkit-scrollbar-thumb {
    background: #452c63;
    border-radius: 20px;
  }
  .titledesign {
    margin-left: 5px;
    padding-left: 5px;
    position: absolute;
  }
}

#title2 {
  margin-top: 5px;
  margin-left: 13px;
  font-size: 15px;
  color: #452c63;
}

.you {
  position: sticky;
  left: 0;
  background-color: black;
}

.close {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  background-color: black;
  padding-top: 8px;
}
.cross {
  width: auto;
  height: 10%;
  background-color: black;
}
#iclose {
  background-color: black;
}
#iclose:hover {
  background-color: #a90c0c;
  cursor: pointer;
}
.y1 {
  width: 100%;
  height: 50%;
}
