body{
    overflow-x: hidden;
    &::-webkit-scrollbar {
        width: 12px;
      }
      &::-webkit-scrollbar-track {
        box-shadow: inset 0 0 5px #5209bf;
        border-radius: 10px;
      }
      &::-webkit-scrollbar-thumb {
        background: #684f86;
        border-radius: 20px;
      }
      .titledesign {
        margin-left: 5px;
        padding-left: 5px;
        position: absolute;
      }
}


