.banner {
  background-size:100% auto;
  background-repeat: no-repeat;
  border-style: solid;
  border-radius: 10px;
  border-color: #452c63;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin: 0;
  height: auto;
}
#mtitle {
  padding-top: 30%;
  background-color: transparent;
  margin-left: 5px;
  font-size: 3vw;
}
.overview {
  padding-top: 2%;  
  padding-bottom: 2%;
  width:50%;
  background-color: transparent;
  margin-left: 5px;
  font-size: 1vw;
}
